import React, { useEffect, useRef } from 'react'

function FocusInput() {
    // 1 宣告ref並初始化
    const inputRef = useRef(null)

    useEffect(()=>{
    // 3 載入頁面時input就自動被focus
    // useRef 會回傳一個含有current這個屬性的物件
        console.log(inputRef)
        inputRef.current.focus()
    },[])
    
  return (
    <div>
        {/* 2 連結宣告好的ref */}
        <div>
            <label for = "account">帳號</label>
            <input type="text" id="account" placeholder='example@email.com' ref={inputRef}/>
        </div>
        <div>
            <label for = "account">密碼</label>
            <input type="text" id="account" placeholder='**********'/>
        </div>
    </div>
  )
}

export default FocusInput