// reducer : 負責確認現在要哪個action，進而執行並改變state
const counterReducer = (state=0, action) =>{
    switch (action.type){
      case "INCREMENT":
        return state + action.payload;
      case "DECREMENT":
        return state - action.payload;
      default:
        return state;
    }
  };

export default counterReducer;